        <div class="row d-flex justify-content-center" id="procedimientos" >
            <div class="col-lg-10 mb-4 order-0 ">
                <div class="card">
                    <div class="d-flex align-items-end row">
                        <div class="card-body pb-4 px-0 px-md-4 h-100">
                            <div class="card-body">
                                <h3 class="fw-bold text-primary">PROCEDIMIENTOS</h3>
                                <br>
                                <h4 class="fw-bold">Unidad Técnica de Atención Integral a Víctimas y Género</h4>
                                <ol>
                                    <li>Seguimiento y evaluación de la Política Institucional de Igualdad de Género (PIIG)</li>
                                    <li>Elaboración del Plan Anual Operativo de la estructura y asesorar la transversalización del enfoque de género en el PAO institucional.</li>
                                    <li>Elaboración de informes requeridos por las diferentes autoridades de la Corte Suprema de Justicia, así como los informes solicitados por diferentes organismos e instituciones en materia de género e igualdad.</li>
                                    <li>Elaboración de propuestas de proyectos para establecer alianzas estratégicas y convenios con organismos de cooperaciones nacionales e internacionales.</li>
                                    <li>Trámite para la Autorización de Apoyo Técnico y/o Fondos de Convenios de Cooperación conforme a los lineamientos institucionales.</li>
                                </ol>
                                <br>
                                <h4 class="fw-bold">Departamento de Género</h4>
                                <ol>
                                    <li>Realización de procesos de capacitación continua y sensibilización sobre género y derechos humanos de las mujeres para el personal del Órgano Judicial.</li>
                                    <li>Realización de campañas de divulgación de la Política Institucional de Igualdad de Género (PIIG).</li>
                                    <li>Monitoreo al cumplimiento de la Política Institucional de Igualdad de Género.</li>
                                    <li>Elaboración de estudios y asistencia técnica en materia de Género.</li>
                                </ol>
                                <br>
                                <h4 class="fw-bold">Equipos Multidisciplinarios Especializados para una Vida Libre de Violencia y Discriminación para las Mujeres</h4>
                                <ul>
                                    <li> Realizar los diferentes tipos de estudios a través de la multidisciplina que sean solicitados por el juzgado competente.</li>
                                    <li> Emitir opiniones, con perspectiva de género y derechos humanos, en asuntos relacionados con la imposición de medidas de protección y cautelares, así como para modificar, sustituir o revocar las mismas, cuando sea requerido por las juezas y jueces de la jurisdicción especializada de violencia y discriminación contra la Mujer (LEIV).</li>
                                    <li> Asistir a las audiencias, cuando sea requerido por la jueza o el juez que ventila la causa judicial.</li>
                                    <li> Colaborar en el seguimiento y revisión de las medidas de protección y cautelares impuestas por las juezas y jueces de la Jurisdicción Especializada para una Vida Libre de Violencia y Discriminación para las Mujeres (LEIV), a fin de constatar que se cumplan los fines para los cuales fueron impuestas.</li>
                                </ul> 
                                <br>
                                <h3>Secciones de Atención Integral a Víctimas</h3>                                
                                <h4 class="fw-bold">Atención en caso de una víctima de Violencia Intrafamiliar o género (según la LEIV)</h4>
                                <ol>
                                    <li>La víctima puede acudir de forma individual o por medio de referencia de un tribunal.</li>
                                    <li>Si las víctimas de violencia inicialmente no quieren interponer la denuncia, de igual forma se le atiende en las áreas de atención psicológica, asesoría legal, médica, lúdica e incluso orientación ciudadana por parte de UNIMUJER, puede ser presencial o vía teléfono.</li>
                                    <li>En el caso que la víctima desee interponer una denuncia, se solicita la dirección de la persona agresora, ya sea del domicilio donde reside o del lugar donde labora, si es posible.</li>
                                    <li>Definir el parentesco con la persona agresora cuando se trate de un caso de violencia Intrafamiliar.</li>
                                    <li>La víctima debe proporcionar sus datos personales y de contacto.</li>
                                    <li>Brindar a la usuaria las terapias en los horarios establecidos por la institución, dependiendo de su disponibilidad.</li>
                                </ol>
                                <br>
                                <h4 class="fw-bold">Si la víctima es referida por una instancia judicial o institución:</h4>
                                <ol>
                                    <li>Debe llevar oficio u hoja de remisión, que determine el motivo de atención siempre y cuando sea de las tipologías que se atienden en las diferentes secciones de atención (C.J. Isidro Menéndez, J. de Soyapango, C.J. de Ciudad Delgado y Chalatenango).</li>
                                    <li>En caso que la víctima sea una niña o adolescente se atiende por medio de referencia, mediante un proceso de denuncia o ha solicitud de la madre, padre o responsable. Siempre y cuando sea de las tipologías que se atienden en las secciones de atención.</li>
                                    <li>De igual forma, la atención se brinda cuando la niña o adolescente es víctima directa de violencia y desea hacer efectivo su derecho de Acceso a la Justicia y el de Petición y Respuesta de conformidad a los 51 y 92 de la Ley de Protección Integral de la Niñez y Adolescencia (LEPINA).</li>
                                </ol>
                              </div>
                        </div>                        
